.background {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 350px;
	background-repeat: repeat-y;
	background-attachment: fixed;
	background-size: cover;
	background-color: #2E3192;
}

.background>img {
	background-color: white;
	border-radius: 50%;
	max-height: 200px;
}